import React from 'react'
import PropTypes from 'prop-types'

import Image from './Image'
import './Card.css'

const Card = ({
  title,
  backgroundImage,
  large,
  className = ''
}) => {
  if (large) className += ' Card-large'
  return (
    <div className={`relative ${className}`}>
      {backgroundImage && (
        <Image
          background
          className="Card"
          resolutions="large"
          src={backgroundImage}
          alt={title}
          size="cover"
        />
      )}
      <div className="container relative CardText">
        <h1 className="Card--Title">{title}</h1>
      </div>
    </div>
  )
}

Card.propTypes = {
  title: PropTypes.string
}

export default Card
